import * as React from 'react';
import { useState } from 'react';
import { HashRouter as Router, Routes, Route } from 'react-router-dom';
import Home from './Home';
import Settings from './Settings';
import SideNav from './SideNav';
import GridOnIcon from '@mui/icons-material/GridOn';
import SettingsIcon from '@mui/icons-material/Settings';

const sections = [{ icon: <GridOnIcon />, text: 'File input', path: 'home'}, {icon: <SettingsIcon />, text:  'Settings', path: 'settings' }]

export default function App(props) {
	const [xlsData, setXlsData] = useState({ name: [], contents: [] });
	const [selectedSheet, setSelectedSheet] = useState([]);
	return (
		<div>
			<Router basename='/'>
				<SideNav appTitle='Files input consistency checker' sections={ sections }/>
				<Routes>
				<Route 
          path='/Home' 
          element={<Home 
            xlsData={xlsData} 
            setXlsData={setXlsData} 
            selectedSheet={selectedSheet} 
            setSelectedSheet={setSelectedSheet}
          />} 
				/>
				<Route 
					path='/Settings' 
					element={<Settings/>} 
				/>
				</Routes>
			</Router>
		</div>
	);
}
