import * as React from 'react';
import Page from './Page.js';
import TableViewer from './TableViewer.js';

export default function Home({ xlsData, setXlsData, selectedSheet, setSelectedSheet }) {

  return (
    <Page name='File input'>
      <TableViewer 
        xlsData={xlsData} 
        setXlsData={setXlsData} 
        selectedSheet={selectedSheet} 
        setSelectedSheet={setSelectedSheet}
      />
    </Page>
  );
}
