import React from "react";
import Container  from '@mui/material/Container';
import ButtonGroup from '@mui/material/ButtonGroup';
import Button from '@mui/material/Button';

export default function SheetsSelect({ xlsData, selectedSheet, setSelectedSheet, setIsLoading, isLoading }) {
  
  return ( 
    xlsData.contents.length? (
      <Container>
        <ButtonGroup variant="contained" aria-label="outlined primary button group">
        {xlsData.contents.map((x, index) =>
          <Button 
            key={index}
            onClick={() => (setSelectedSheet(index), setIsLoading(true))}
            sx={{
              ...(index == selectedSheet && {
                backgroundColor: '#1976d2',
                color: 'white'
              }), 
              ...(index != selectedSheet && {
                backgroundColor: 'white',
                color: '#000'
              }),
              '&:hover': {
                color: 'white',
              },
            }}
          >
            {x.sheetname}
          </Button>
        )}
        </ButtonGroup>
      </Container>
    ) : null
  );
}
