import * as React from 'react';
import { read, utils } from 'xlsx';
import { useState } from 'react';
import SheetsSelect from './SheetsSelect.js'
import BasicTable from './BasicTable';
import Container  from '@mui/material/Container';
import CircularProgress  from '@mui/material/CircularProgress';
import Button from '@mui/material/Button';
import InputLabel from '@mui/material/InputLabel';

let workbook;

async function handleFile(e) {
  let reader = new FileReader();
  const readFile = () => {
    return new Promise((resolve, reject) => {
      reader.onload = async function(e) {
        try {
          const response = read(e.target.result, {type: 'binary'});
          resolve(response);
        } catch (err) {
          reject(err);
        }
      };
      reader.onerror = (err) => {
        reject(err);
      };
      reader.readAsBinaryString(e.target.files[0]);
    });
  };
  let result = await readFile(e);
  return result;
};

function parseFile(workbook) {
  let contents = [];
  for (let i = 0; i < workbook.SheetNames.length; i++) {
    let sheetname = workbook.SheetNames[i];
    let worksheet = workbook.Sheets[sheetname];
    let headerRow = 0;
    let header = utils.sheet_to_json(worksheet, { range: headerRow, header: 1, blankrows: false })[0];
    let indexedHeader = ['Row #'];
    for (let j = 0; j < header.length; j++) {
      header[j] = header[j] === undefined ? 'EMPTY' : header[j];
      let count = header.slice(0, j).filter(x => x === header[j]).length;
      indexedHeader.push(header[j] + (count ? '_' + count : ''));
    };
    let data = utils.sheet_to_json(worksheet, { range: headerRow, header: indexedHeader.slice(1) }).slice(1);
    data = data.map((x) => ({ 'Row #': x.__rowNum__, ...x }));
    contents.push({sheetname: sheetname, header: indexedHeader, data: data});
  };
  return contents
};

export default function TableViewer({ xlsData, setXlsData, selectedSheet, setSelectedSheet }) {
  const [isLoading, setIsLoading] = useState(false);

  async function loadFile(e) {
    setIsLoading(true);
    workbook = await handleFile(e);
    let contents = parseFile(workbook)
    setXlsData({ name: e.target.files[0].name, contents: contents });
    setSelectedSheet(0);
  }

  return (
    <Container>
      <Container sx={{ justifyContent: 'center', display: 'inline-flex' }}>
        <Button variant="contained" component="label">
          Upload
          <input 
            hidden 
            accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" 
            type="file"
            id="input" 
            onChange={(e) => loadFile(e)}
            disabled={isLoading}
          />
        </Button>
        <InputLabel sx={{ paddingLeft: '1rem' }}> { xlsData.name } </InputLabel>
      </Container>
      <br/>
      <CircularProgress disableShrink={true} size={100} thickness={6}
        sx={{
          top: '350px', left: 'calc(50% - 50px)', position: 'absolute', color:"#bbdefb",
          ...(!isLoading && {
            visibility: 'hidden',
          })
        }}
      />
      <br/>
      <Container sx={{...(isLoading && {visibility: 'hidden'})}}>
        <SheetsSelect 
          xlsData={xlsData} 
          selectedSheet={selectedSheet}
          setSelectedSheet={setSelectedSheet}
          setIsLoading={setIsLoading} 
          isLoading={isLoading} 
        />
        <br/>
        <BasicTable 
          xlsData={xlsData} 
          selectedSheet={selectedSheet}
          setIsLoading={setIsLoading} 
        />
      </Container>
      <br/>
    </Container>
  );
}
