import * as React from 'react';
import { useEffect, useState } from 'react';
import Table from '@mui/material/Table';
import { styled } from '@mui/material/styles';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

const StyledTableCellFirst = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    position: 'sticky',
    left: 0,
    zIndex: 11,
    backgroundColor: '#1565c0',
    color: theme.palette.common.white,
    padding: 5,
    minWidth: 60
  },
  [`&.${tableCellClasses.body}`]: {
    position: 'sticky',
    left: 0,
    zIndex: 10,
    backgroundColor: '#42a5f5',
    color: theme.palette.common.white,
    fontSize: 14,
    padding: 5
  },
}));

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: theme.palette.primary.main,
    color: theme.palette.common.white,
    padding: 5,
    minWidth: 200
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
    padding: 5
  },
}));

  
const StyledTableRow = styled(TableRow)(({ theme }) => ({
  '&:nth-of-type(odd)': {
    backgroundColor: theme.palette.action.hover,
  },
  '&:last-child td, &:last-child th': {
    border: 0,
  },
}));

  
export default function BasicTable({ xlsData, selectedSheet, setIsLoading }) { 
  useEffect(() => {
    setIsLoading(false)
  });
  return (
    typeof(xlsData.contents[selectedSheet]) !== 'undefined'? (
      <TableContainer sx={{ maxHeight: '600px', width: '100%', height: 'max-content' }} component={Paper}>
        <Table stickyHeader aria-label="simple table">
          <TableHead>
            <TableRow>
            <StyledTableCellFirst key={0}>{xlsData.contents[selectedSheet].header[0]}</StyledTableCellFirst>
            {xlsData.contents[selectedSheet].header.slice(1).map((h, index) => (
              <StyledTableCell key={index}>{h}</StyledTableCell>
            ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {xlsData.contents[selectedSheet].data.map((row) => (
              <StyledTableRow
                key={row['Row #']}
                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
              >
                <StyledTableCellFirst key={0}>{row[xlsData.contents[selectedSheet].header[0]]}</StyledTableCellFirst>
                {xlsData.contents[selectedSheet].header.slice(1).map((h, index) => (
                  <StyledTableCell key={index}>{row[h]}</StyledTableCell>
                ))}
              </StyledTableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    ) : null
  );
}